import React, {Component} from 'react';
import {
    IcoChat_B,
    IcoChat_C,
    IcoChat_D,
    IcoChat_E,
    IcoChat_F
} from '../iconsSvg';
import Logo from './logo.png';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";

class ChatMainIcon extends Component {
    state = {
        currentSlide: 0,
        pos: {
            x: document.documentElement.clientWidth - 100,
            y: document.documentElement.clientHeight - 100
        },
    }

    componentDidMount() {
        window.addEventListener('resize', this.resize);
    }

    componentDidUpdate(props, state) {
        if (this.state.dragging && !state.dragging) {
            document.addEventListener('mousemove', this.onMouseMove);
            document.addEventListener('mouseup', this.onMouseUp);
        } else if (!this.state.dragging && state.dragging) {
            document.removeEventListener('mousemove', this.onMouseMove);
            document.removeEventListener('mouseup', this.onMouseUp);
        }
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.resize);
    }

    onMouseDown = e => {
        if (e.button !== 0) return;

        let pos = document.getElementById("mainIcon");

        this.setState({
            dragging: true,
            rel: {
                x: e.pageX - pos.offsetLeft,
                y: e.pageY - pos.offsetTop
            }
        });
        e.stopPropagation();
        e.preventDefault();
    };

    onMouseUp = e => {
        let mainIcon = this.mainIcon.getBoundingClientRect();
        let windowWidth = window.innerWidth;
        let windowHeight = window.innerHeight;

        this.setState({dragging: false});

        if (mainIcon && mainIcon.x < 0) {
            this.setState({
                pos: {x: 5}
            });
        }
        if (mainIcon && mainIcon.right > windowWidth) {
            let positionLeft = windowWidth - this.mainIcon.offsetWidth - 5;
            this.setState({
                pos: {x: positionLeft}
            });
        }
        if (mainIcon && mainIcon.y < 0) {
            this.setState({
                pos: {y: 5}
            });
        }
        if (mainIcon && mainIcon.bottom > windowHeight) {
            let positionBottom = windowHeight - this.mainIcon.offsetHeight - 5;
            this.setState({
                pos: {y: positionBottom}
            });
        }

        e.stopPropagation();
        e.preventDefault();
    };

    onMouseMove = e => {
        if (!this.state.dragging) return;

        this.setState({
            pos: {
                x: e.pageX - this.state.rel.x,
                y: e.pageY - this.state.rel.y
            }
        });
        e.stopPropagation();
        e.preventDefault();
    };

    changeBackgroundColorOnSlide = () => {
        switch (this.state.currentSlide) {
            case 0:
                return window.STYLE_MAIN_ICON_BACKGROUND_COLOR_1;
            case 1:
                return window.STYLE_MAIN_ICON_BACKGROUND_COLOR_2;
            case 2:
                return window.STYLE_MAIN_ICON_BACKGROUND_COLOR_3;
            case 3:
                return window.STYLE_MAIN_ICON_BACKGROUND_COLOR_4;
            case 4:
                return window.STYLE_MAIN_ICON_BACKGROUND_COLOR_5;
            case 5:
                return window.STYLE_MAIN_ICON_BACKGROUND_COLOR_6;
        }
    }

    resize = () => {
        this.setState({
            pos: {
                x: document.documentElement.clientWidth - 100,
                y: document.documentElement.clientHeight - 100
            }
        });
    }

    render() {
        let {
            handleVisibleChat,
            autoplayMainIcon,
            visibleCounterNewMessage,
            oldCounterHistoryMessage,
            openingChat,
        } = this.props;

        let chatLanguage = document.getElementById('chatLanguage');
        let lang = chatLanguage.getAttribute('data-language');

        const renderSliderItem = (() => {
            const settings = {
                infinite: false,
                speed: 500,
                slidesToShow: 1,
                slidesToScroll: 1,
                // autoplay: autoplayMainIcon,
                autoplay: false,
                autoplaySpeed: 2000,
                swipe: false,
                beforeChange: (current, next) => {
                    this.setState({currentSlide: next})
                }
            };

            return (
                <Slider {...settings}>
                    {window.VISIBLE_CHAT_ICON && <div className="sm-chat_slide sm-mainIco-left_1">
                         <span>
                            <img src={Logo} style={{width: '60px'}}/>
                        </span>
                        {visibleCounterNewMessage && oldCounterHistoryMessage > 0 &&
                        <span className="sm-counterMessage">{oldCounterHistoryMessage}</span>}
                    </div>}
                    {window.LINK_TELEGRAM && <div className="sm-mainIco-left sm-mainIco-left_2">{IcoChat_C}</div>}
                    {window.LINK_VIBER && <div className="sm-mainIco-left_3">{IcoChat_E}</div>}
                    {window.LINK_FACEBOOK && <div className="sm-mainIco-left_4">{IcoChat_B}</div>}
                    {window.LINK_SKYPE && <div className="sm-mainIco-left_5">{IcoChat_D}</div>}
                    {window.LINK_INSTAGRAM && <div className="sm-mainIco-left_6">{IcoChat_F}</div>}
                </Slider>
            )
        })();

        return (
            <div className="sm-chat__main__icon"
                 onDoubleClick={() => handleVisibleChat(true)}
                 onClick={() => {
                     if (window.CHAT_ICON_WITH_NOTIFICATION) {
                         this.onMouseDown
                     } else {
                         handleVisibleChat(true);
                     }
                 }}
                 onMouseDown={this.onMouseDown}
                 id="mainIcon"
                 style={{left: this.state.pos.x + 'px', top: this.state.pos.y + 'px'}}
                 ref={node => (this.mainIcon = node)}
            >
                <div
                    className={`hexagon ${this.state.currentSlide === 0 ? 'with_box-shadow' : ''} ${window.MAIN_ICON_FORM && window.MAIN_ICON_FORM === 'circle' ? 'circle-important' : ''} ${this.state.currentSlide === 3 ? 'with_instagram' : ''}`}
                    style={{background: this.changeBackgroundColorOnSlide()}}
                >
                    <span className="hexagon_before"
                          style={{background: this.changeBackgroundColorOnSlide()}}
                    />
                    {renderSliderItem}
                    <span className="hexagon_after"
                          style={{background: this.changeBackgroundColorOnSlide()}}
                    />
                    {window.CHAT_ICON_WITH_NOTIFICATION && !openingChat && <div className="sm-notification">
                        <span className="sm-element"
                              style={{backgroundColor: window.STYLE_MAIN_ICON_BACKGROUND_COLOR ? window.STYLE_MAIN_ICON_BACKGROUND_COLOR : '#005ba8'}}/>
                        <span>
                            {lang !== '' && lang === 'ua' || lang === 'ukr' && window.MAIN_ICON_ANIMATION_UA ? window.MAIN_ICON_ANIMATION_UA : null}
                            {lang !== '' && lang === 'ru' || lang === 'rus' && window.MAIN_ICON_ANIMATION_RU ? window.MAIN_ICON_ANIMATION_RU : null}
                            {lang !== '' && lang === 'en' || lang === 'eng' && window.MAIN_ICON_ANIMATION_EN ? window.MAIN_ICON_ANIMATION_EN : null}
                        </span>
                    </div>}
                </div>
            </div>
        );
    }
}

export default ChatMainIcon;



