import React, {Component} from "react";
import {PAYLOAD} from "../payloads";
import receiverLogo from './receiver-logo.png'
// sm-operator

class Receiver extends Component {
    state = {
        hours: null,
        minutes: null,
    };

    componentDidMount() {
        this.formatDate()
    }

    formatDate = () => {
        const date = new Date();
        let hours = date.getHours();
        if (hours < 10) hours = "0" + hours;
        let minutes = date.getMinutes();
        if (minutes < 10) minutes = "0" + minutes;

        this.setState({hours, minutes})
    };

    render() {
        let {onMessage, isShow, time, data, config, handlerCarouselLength} = this.props;

        if(data && data.type === "CAROUSEL" && data.cards) {
            handlerCarouselLength(data.cards.length);
        }

        if (PAYLOAD(data) !== undefined) {
            return (
                <div className={`receiver ${time ? 'withTime' : ''}`}>
                    <img src={receiverLogo} style={{width: '30px', height: '30px'}}/>
                    <div className="content">
                        <div className="w-sm-header">
                            {PAYLOAD(data, onMessage, isShow, config)}
                            {time ? <span className="timer">{time}</span> : <span className="timer client-timer">{this.state.hours}:{this.state.minutes}</span>}
                        </div>
                    </div>
                </div>
            )
        } else {
            return false;
        }
    }
}

export default Receiver;


